.theme-toggle {
    position: fixed;
    top: 20px;
    right: 20px;
    background-color: transparent; /* Fundo transparente */
    border: none; /* Sem borda */
    color: #13293D; /* Cor do ícone */
    font-size: 24px; /* Tamanho do ícone */
    cursor: pointer; /* Cursor de ponteiro ao passar o mouse */
    z-index: 1000; /* Para garantir que o botão fique acima de outros elementos */
    transition: color 0.3s; /* Transição suave para mudança de cor */
  }
  
  .theme-toggle:hover {
    color: #1B98E0; /* Cor do ícone ao passar o mouse */
  }
  
  body.dark-mode .theme-toggle {
    color: #E8F1F2; /* Cor do ícone no tema escuro */
  }
  
  body.dark-mode .theme-toggle:hover {
    color: #A0C4FF; /* Cor do ícone ao passar o mouse no tema escuro */
  }
  