@charset "UTF-8";
@import url(https://fonts.googleapis.com/earlyaccess/cwtexyen.css);
@import url(https://fonts.googleapis.com/css?family=Josefin+Sans);
@import url(https://fonts.googleapis.com/css?family=Source+Code+Pro:400,500);

:root {
  --background-color-light: #E8F1F2;
  --background-color-dark: #1A1A1A;
  --text-color-light: #13293D;
  --text-color-dark: #E8F1F2;
  --link-color-light: #1B98E0;
  --link-color-dark: #A0C4FF;
}

@keyframes change-text {
  0%, 55%, 100% {
    transform: translateY(-45%);
  }
  5%, 50% {
    transform: translateY(-95%);
  }
}
@keyframes typing {
  0% {
    width: 0;
  }
}
@keyframes text-blink {
  50% {
    border-color: transparent;
  }
}

body {
  margin: 0;
  box-sizing: border-box;
  color: var(--text-color-light);
  background-color: var(--background-color-light);
  font-family: "Josefin Sans", cwtexyen, sans-serif;
}

body.dark-mode {
  color: var(--text-color-dark);
  background-color: var(--background-color-dark);
}

body.dark-mode a {
  color: var(--link-color-dark);
}

a {
  text-decoration: none;
  color: var(--link-color-light);
}

a:hover {
  color: #13293D;
}

/* css Heading*/
.heading {
  /*border: 2px solid #007BFF; /* Borda debug */
  padding: 1%; /* Espaço interno */
  margin: 0px; /* Espaço externo */
  text-align: center;
  background-color: var(--background-color-light);
}

body.dark-mode .heading {
  background-color: #333; /* Cor de fundo para o tema escuro */
}

.heading h1 {
  margin: 0 auto;
  font-family: "Source Code Pro", monospace;
  font-size: 50px;
  font-weight: 500;
  animation: 8s typing steps(16, end), 0.5s text-blink step-end infinite;
  width: 9.6em;
  white-space: nowrap;
  overflow: hidden;
  border-right: 3px solid;
}

@media screen and (max-width: 550px) {
  .heading h1 {
    font-size: 30px;
  }
}

.sm-name {
  display: none;
  color: #006494;
}
@media screen and (max-width: 550px) {
  .sm-name {
    display: block;
  }
}

.name {
  margin-top: 10px;
  height: 52px;
  overflow: hidden;
}
@media screen and (max-width: 550px) {
  .name {
    display: none;
  }
}
.name ul {
  list-style: none;
  padding: 0;
  color: #006494;
  font-size: 40px;
  animation: 10s change-text linear infinite;
}
.name ul li {
  padding-top: 5px;
  line-height: 52px;
}

.social ul {
  list-style: none;
  padding-left: 0;
}
.social ul a {
  color: var(--text-color-light);
}

body.dark-mode .social ul a {
  color: var(--text-color-dark);
}

.social ul li {
  display: inline-block;
  margin: 0 5px;
}

.profile {
  background-size: 100%;
  border-radius: 50%;
  margin: 20px auto;
  width: 180px;
  height: 180px;
  display: inline-block;
  background-image: url(https://media.licdn.com/dms/image/v2/D4E03AQHWvebZzW2l0w/profile-displayphoto-shrink_200_200/profile-displayphoto-shrink_200_200/0/1721406316349?e=1733961600&v=beta&t=mV4syRBKx_hrrBZSFW2RsW8Yxpnqr8db0fTq9e4Rqck);
}

/* Fim css Heading*/

/* css main*/
.wrapper {
  margin: 5% 10%;
}

.block h2::first-letter {
  font-size: 30px;
  color: #006494;
}

.block p{
  font-size: 1.3em;
}

.block h2 {
  font-family: "Source Code Pro", monospace;
  border-bottom: 2px solid;
}
.block ul {
  font-size: 1.3em;
  list-style: none;
  padding-left: 0;
}

.info .email::before {
  content: "jacivaldocarvalho@gmail.com";
}

.research {
  color: #AD0000;
}

.pri-tag {
  display: inline-block;
  padding: 8px;
  background: #13293D;
  color: #E8F1F2;
  margin: 5px;
  border-radius: 5px;
}

.sec-tag {
  display: inline-block;
  padding: 8px;
  background: #E8F1F2;
  color: #13293D;
  margin: 5px;
  border-radius: 5px;
}

.degree h3, .exp-item h3 {
  margin: 10px 0 5px 0;
  padding: 0;
}
.degree p, .exp-item p {
  margin: 0;
  padding: 0;
}

.exp-item ul {
  list-style: square;
  padding-left: 15px;
}
.exp-item ul li {
  padding-top: 5px;
}
/* fim css main*/

/*Css Footer*/

footer {
  background-color: #E8F1F2; /* Cor de fundo para o modo claro */
  color: #333; /* Cor do texto */
  padding: 10px;
  text-align: center;
  position: relative;
}

footer .social-links {
  margin: 10px 0;
}

footer .social-links a {
  margin: 0 15px; /* Espaçamento entre os ícones */
  text-decoration: none;
  color: #007bff; /* Cor dos links */
  transition: color 0.3s;
}

footer .social-links a:hover {
  color: #0056b3; /* Cor ao passar o mouse */
}

footer p {
  margin: 10px 0 0;
  font-size: 0.9em; /* Tamanho da fonte do texto de direitos autorais */
}

body.dark-mode footer {
  background-color: #333; /* Cor de fundo para o tema escuro */
  color: #E8F1F2; /* Cor do texto no tema escuro */
}

body.dark-mode footer .social-links a {
  color: #A0C4FF; /* Cor dos links no tema escuro */
}

body.dark-mode footer .social-links a:hover {
  color: #E8F1F2; /* Cor ao passar o mouse no tema escuro */
}

/*Fim css Footer*/


