.floating-button {
    position: fixed;
    bottom: 20px;
    right: 20px;
    background-color: #006494; /* Cor de fundo */
    color: white; /* Cor do texto */
    border: none; /* Sem borda */
    border-radius: 50%; /* Botão circular */
    width: 50px; /* Largura do botão */
    height: 50px; /* Altura do botão */
    font-size: 24px; /* Tamanho da fonte */
    display: flex; /* Flex para centralizar o conteúdo */
    align-items: center; /* Alinhamento vertical */
    justify-content: center; /* Alinhamento horizontal */
    cursor: pointer; /* Mudar cursor ao passar o mouse */
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2); /* Sombra */
    transition: background-color 0.3s, transform 0.3s; /* Transição suave */
}

.floating-button:hover {
    background-color: #0056b3; /* Cor de fundo ao passar o mouse */
    transform: scale(1.1); /* Efeito de aumento ao passar o mouse */
}

.floating-button:focus {
    outline: none; /* Remover contorno ao focar */
}
